<template>
    <h1>Pedidos guardados</h1>

    <div>
        <a
            class="list-group-item list-group-item-action list-group-item-light d-flex justify-content-between align-items-center"
            v-for="(pedido, index) in pedidos"
            :key="pedido"
            @click="push(pedido)"
        >
            <span class="d-flex align-items-center">{{ pedido.cliente }}</span>
            <span class="d-flex align-items-center">{{ pedido.fecha }}</span>
        <!--    <span class="d-flex align-items-center">{{ pedido }}</span> -->
            <span class="my-4">Pedido N # {{ index }}</span>
        </a>
    </div>
</template>

<script>
import { useStore } from "vuex";

export default {
    setup() {
        const store = useStore();
        const pedidos = store.getters.getPedidos;

        
        return { store, pedidos }
    },
    methods: {
        push(pedido) {
            let pedidocp = JSON.parse(JSON.stringify(pedido));
            this.store.commit("agregarCliente", (pedidocp.cliente));
            delete pedidocp.cliente;
            delete pedidocp.fecha;
            this.store.commit("agregarPedidoBorrador", ({ ...pedidocp }));
            this.$router.push("/pedidos/");
        },
    }
}
</script>

